import React, { useState, lazy, Suspense, useEffect } from 'react';

import './Content.css';

import woodTexture from '../../img/wood.png';
import signBase from '../../img/signbase.png';

const Avatar = lazy(() => import('./Avatar/Avatar'));
const Home = lazy(() => import('./Home/Home'));
const AboutMe = lazy(() => import('./AboutMe/AboutMe'));
const Work = lazy(() => import('./Work/Work'));
const Portfolio = lazy(() => import('./Portfolio/Portfolio'));
const Contact = lazy(() => import('./Contact/Contact'));

const Content = ({ name }) => {
  const greetings = name !== '' ? `Hello, ${name}!` : 'Hello, fellow traveler of the interwebz!';
  const [dialogText, setDialogText] = useState(greetings);
  const [currentAction, setCurrentAction] = useState('');

  const renderRightSideComponent = () => {
    const loader = (
      <div id="loader">
        <i className="spinner icon"></i>
      </div>
    );
    switch (currentAction) {
      case 'goTo-about':
        return (
          <Suspense fallback={loader}>
            <AboutMe />
          </Suspense>
        );
      case 'goTo-work':
        return (
          <Suspense fallback={loader}>
            <Work setDialogText={setDialogText} setCurrentAction={setCurrentAction} />
          </Suspense>
        );
      case 'goTo-portfolio':
        return (
          <Suspense fallback={loader}>
            <Portfolio />
          </Suspense>
        );
      case 'goTo-contact':
        return (
          <Suspense fallback={loader}>
            <Contact visitor={name} setDialogText={setDialogText} />
          </Suspense>
        );
      default:
        return (
          <Suspense fallback={loader}>
            <Home setDialogText={setDialogText} setCurrentAction={setCurrentAction} />
          </Suspense>
        );
    }
  };

  useEffect(() => {
    document.querySelector('#player-controls').style.display = 'block';
    document.querySelector('#bgMusic').volume = 0.1;
    document.querySelector('#bgMusic').play();
  }, []);

  return (
    <Suspense
      fallback={
        <div id="loader">
          <i className="spinner icon"></i>
        </div>
      }
    >
      <div className="ui container main">
        <div className="ui stackable grid">
          <div className="seven wide column">
            <div className="left-side-wrapper">
              <Avatar
                visitor={name}
                dialogText={dialogText}
                setDialogText={setDialogText}
                currentAction={currentAction}
                setCurrentAction={setCurrentAction}
              />
            </div>
          </div>
          <div className="nine wide column">
            <div className="right-side-wrapper">
              <div id="sign-shadow"></div>
              <div id="sign" style={{ backgroundImage: `url(${woodTexture})` }}>
                {renderRightSideComponent()}
              </div>
              <div id="sign-base" style={{ backgroundImage: `url(${signBase})` }}></div>
            </div>
          </div>
        </div>
      </div>
    </Suspense>
  );
};

export default Content;
